const LessonState = props => {
  const initialState = {
    lessons: [],
    currentLesson: null,
    loading: false,
    error: null
  };

  const [state, dispatch] = useReducer(lessonReducer, initialState);

  // Get all lessons
  const getLessons = async () => {
    setLoading();

    try {
      // For demo purposes, use the demo data instead of API call
      // const res = await axios.get('/api/lessons');
      
      setTimeout(() => {
        dispatch({
          type: GET_LESSONS,
          payload: demoLessons
        });
      }, 500); // Simulate network delay
    } catch (err) {
      dispatch({
        type: LESSON_ERROR,
        payload: 'Error loading lessons'
      });
    }
  };

  // Get a specific lesson
  const getLesson = async (id) => {
    setLoading();

    try {
      // For demo purposes, use the demo data instead of API call
      // const res = await axios.get(`/api/lessons/${id}`);
      
      // Find the lesson in demo data
      const lesson = demoLessons.find(lesson => lesson.id === id);
      
      setTimeout(() => {
        if (lesson) {
          dispatch({
            type: GET_LESSON,
            payload: lesson
          });
        } else {
          dispatch({
            type: LESSON_ERROR,
            payload: 'Lesson not found'
          });
        }
      }, 500); // Simulate network delay
    } catch (err) {
      dispatch({
        type: LESSON_ERROR,
        payload: 'Error loading lesson'
      });
    }
  };

  // Clear current lesson
  const clearLesson = () => {
    dispatch({ type: CLEAR_LESSON });
  };

  // Set loading
  const setLoading = () => {
    dispatch({ type: SET_LOADING });
  };

  return (
    <LessonContext.Provider
      value={{
        lessons: state.lessons,
        currentLesson: state.currentLesson,
        loading: state.loading,
        error: state.error,
        getLessons,
        getLesson,
        clearLesson
      }}
    >
      {props.children}
    </LessonContext.Provider>
  );
};

export default LessonState; 